#highload1 {
  display: none;
}

#highload1 + .lb1,
#highload1 + .lb1::before,
#highload1 + .lb1::after {
  transition: all 0.3s;
}

#highload1 + .lb1 {
  display: inline-block;
  position: relative;
  width: 60px;
  height: 30px;
  border-radius: 100px;
  cursor: pointer;
}
@media only screen and (max-width: 767px) {
  #highload1 + .lb1 {
    width: 38px;
    height: 20px;
  }
}

#highload1 + .lb1::before {
  display: block;
  content: attr(data-offlabel);
  position: absolute;
  left: -77px;
  top: 3px;
  right: auto;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #97a0af;
}
@media only screen and (max-width: 767px) {
  #highload1 + .lb1::before {
    font-size: 12px;
    line-height: 15px;
    left: -70px;
  }
}

#highload1 + .lb1::after {
  border-radius: 50%;
  content: "";
  position: absolute;
  top: 3px;
  left: 3px;
  width: 24px;
  height: 24px;
  background-color: white;
}
@media only screen and (max-width: 767px) {
  #highload1 + .lb1::after {
    width: 16px;
    height: 16px;
    top: 2px;
  }
}

#highload1:checked + .lb1::before {
  content: attr(data-onlabel);
  left: -77px;
  top: 3px;
  right: auto;
  color: #cb94d3;
}
@media only screen and (max-width: 767px) {
  #highload1:checked + .lb1::before {
    left: -70px;
  }
}

#highload1:checked + .lb1::after {
  left: 32px;
  background-color: #f7f7f7;
}
@media only screen and (max-width: 767px) {
  #highload1:checked + .lb1::after {
    left: 19px;
  }
}

#highload1 + .lb1 {
  background-color: #ccc;
}

#highload1:checked + .lb1 {
  background-color: #cb94d3;
}

#highload1:checked + .lb1::before {
  color: #cb94d3;
}

